import React from 'react'
import './Cards.css'
import VirusRed from '../../Assets/virus-red.png'
import VirusBlue from '../../Assets/virus-blue.png'
import VirusGreen from '../../Assets/virus-green.png'
import Counter from 'react-countup';

const Cards = ( { data: {confirmed, recovered, deaths, lastUpdate}}) => {
    if(!confirmed){
        return 'Syncing ⌛'
    }
    
    return (
        <div className="card">
            <div className="infected_card mt-4">
                <div className="card-body">
                    <div className="card-content">
                        <img src={VirusGreen}/>
                        <h4 className="content">Infected : <span className="counter_valuer"></span> <strong className="value"> <Counter start={0} end={confirmed.value} duration={3} separator=","/> </strong></h4>
                        <h4 className="content">Last Update : <strong className="value">{new Date(lastUpdate).toDateString()}</strong></h4>
                    <span className="active_cases"> Displaying total number of infected people by covid </span>
                    </div>
                </div>
            </div>
            <div className="recovered_card mt-4">
                <div className="card-body">
                    <div className="card-content">
                        <img src={VirusBlue}/>
                        <h4 className="content">Recovered : <strong className="value">  <Counter start={0} end={recovered.value} duration={3} separator=","/></strong></h4>
                        <h4 className="content">Last Update : <strong className="value">{new Date(lastUpdate).toDateString()}</strong></h4>
                    <span className="active_cases">Displaying total number of covid recoveries </span>
                    </div>
                </div>
            </div>
            <div className="death_card mt-4">
                <div className="card-body">
                    <div className="card-content">
                        <img src={VirusRed}/>
                        <h4 className="content">Deaths : <strong className="value"> <Counter start={0} end={deaths.value} duration={3} separator=","/> </strong></h4>
                        <h4 className="content">Last Update : <strong className="value">{new Date(lastUpdate).toDateString()}</strong></h4>
                    <span className="active_cases">Displaying total number of deaths caused by covid </span>
                    </div>
                </div>
            </div>
            
        </div>
    )
}

export default Cards
