import '@fortawesome/fontawesome-free/css/all.min.css'; 
import 'bootstrap-css-only/css/bootstrap.min.css'; 
import { MDBIcon } from 'mdbreact';
import 'mdbreact/dist/css/mdb.css';
import React from 'react';
import { NavLink } from 'react-router-dom';
import { fetchData } from './Api';
import './App.css';
import { Cards, Chart, Country } from './components'
import Logo from '../src/Assets/logo-covid-tracker-horizontal.png'
import icon from './Assets/virus-red.png'



class App extends React.Component{
  state = {
    data: {},
    country: ''
  }
  async componentDidMount(){
    const data = await fetchData();
    this.setState({ data })
  }
  handleCountryChange = async (country) => {
    const data = await fetchData(country);
    this.setState({ data , country: country})
  }
  render(){
    const { data, country } = this.state;
    return(
      <div className="App">
      <div className="heading_app mb-5">
        <div className="heading_app_left">
          <img src={Logo}/>
        </div>
        <div className="heading_app_right">
          <span><MDBIcon icon="bars"/></span>
        </div>
      </div>
      <div className="container">
        <Cards data={data}/>
        <Country handleCountryChange={this.handleCountryChange}/>
        <Chart data={data} country={country}/>
      </div>
      <div className="covid_summary container">
        <h1 className="covid">What is C<img src={icon}/>VID 19 ?</h1>
          <hr/>
          <p className="summary">Coronavirus disease (COVID-19) is an infectious disease caused by a newly discovered coronavirus.
            Most people infected with the COVID-19 virus will experience mild to moderate respiratory illness and recover without requiring special treatment.  Older people, and those with underlying medical problems like cardiovascular disease, diabetes, chronic respiratory disease, and cancer are more likely to develop serious illness.
            The best way to prevent and slow down transmission is to be well informed about the COVID-19 virus, the disease it causes and how it spreads. Protect yourself and others from infection by washing your hands or using an alcohol based rub frequently and not touching your face. 
            The COVID-19 virus spreads primarily through droplets of saliva or discharge from the nose when an infected person coughs or sneezes, so it’s important that you also practice respiratory etiquette (for example, by coughing into a flexed elbow).
          </p>
          <button className="btn btn-outline-danger btn-round"><a target="_blank" href="https://github.com/muhammadfaizkhan/covid-tracker" style={{color: "red"}}>READ MORE </a></button>
      </div>
      <div className="footer_app">
        <p className="footer__heading">&copy; Covid Tracker 2021 - Developed by <a target="_blank" href="https://github.com/muhammadfaizkhan">Muhammad Faiz Khan</a></p>
      </div>  
    </div>
    )
  };
}

export default App;
